:root {
  --score-bg-color: #f0f0f0;
}

body {
  background-color: var(--score-bg-color);
  margin: 0;
  padding: 0;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  transition: background-color 1s ease-in-out;
}

#container {
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  display: flex;
}

.container {
  display: flex;
  position: relative;
}

#content {
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  display: flex;
}

#appTitle {
  color: #2d1810;
  text-align: center;
  text-shadow: 2px 2px 4px #daa52066;
  letter-spacing: 2px;
  margin: 20px 0 30px;
  font-family: Cinzel Decorative, cursive;
  font-size: 42px;
  font-weight: 700;
}

#instructions {
  color: #6b4423;
  text-align: center;
  opacity: .8;
  margin: 0 0 20px;
  font-family: Macondo, cursive;
  font-size: 16px;
  font-style: italic;
}

#loading {
  z-index: 1000;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  display: none;
}

#textContainer {
  background-color: #fff;
  border-radius: 5px;
  width: 100%;
  max-width: 650px;
  max-height: 60vh;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 2px 5px #0000001a;
}

@media (width <= 768px) {
  #appTitle {
    letter-spacing: 1px;
    margin: 15px 0 20px;
    font-size: 32px;
  }

  #instructions {
    margin: 0 0 15px;
    padding: 0 10px;
    font-size: 14px;
  }

  #textContainer {
    max-width: calc(100% - 20px);
    margin: 10px;
    padding: 15px;
  }

  #text, #animation {
    font-size: 20px;
    line-height: 1.5;
  }

  .relatedItems {
    padding: 12px 8px;
    font-size: 13px;
  }
}

#categoryContainer {
  width: 100%;
  max-width: 650px;
  margin-bottom: 20px;
}

#categoryBuckets {
  grid-template-columns: repeat(auto-fit, minmax(clamp(70px, 8vw, 100px), 1fr));
  justify-items: center;
  gap: clamp(10px, 2vw, 25px);
  max-width: 100%;
  padding: clamp(10px, 2vw, 20px);
  display: grid;
}

.categoryBucket {
  opacity: .6;
  cursor: pointer;
  flex-direction: column;
  align-items: center;
  padding: clamp(3px, .5vw, 8px);
  transition: opacity .3s, transform .2s;
  display: flex;
  position: relative;
}

.categoryBucket.active {
  opacity: 1;
  transform: scale(1.1);
}

.categoryBucket.receiving {
  opacity: 1;
  filter: brightness(1.2);
  transform: scale(1.2);
  box-shadow: 0 0 10px #ffd70099;
}

.categoryBucket img {
  object-fit: cover;
  border-radius: clamp(6px, .8vw, 12px);
  width: clamp(50px, 5vw, 80px);
  height: clamp(50px, 5vw, 80px);
  margin-bottom: clamp(4px, .6vw, 8px);
  transition: all .3s;
  box-shadow: 0 2px 4px #0000001a;
}

.categoryLabel {
  text-align: center;
  color: #555;
  text-transform: capitalize;
  word-wrap: break-word;
  max-width: 80px;
  font-size: clamp(10px, 1.2vw, 14px);
  font-weight: bold;
  line-height: 1.2;
}

.category-count {
  color: #333;
  background-color: #0000001a;
  border-radius: 8px;
  min-width: 12px;
  margin-top: 2px;
  padding: 1px 4px;
  font-size: 9px;
  font-weight: bold;
  display: inline-block;
}

.categoryBucket.active .category-count {
  color: #000;
  background-color: #ffd700cc;
}

.phrase-highlight {
  background: radial-gradient(at 20% 30%, #daa52066 0%, #0000 50%), radial-gradient(at 80% 70%, #cd853f4d 0%, #0000 50%), radial-gradient(at 40% 80%, #deb88759 0%, #0000 50%), linear-gradient(135deg, #f0e68c40 0%, #daa52033 100%);
  border-radius: 8px;
  padding: 2px 4px;
  transition: all .5s;
  position: relative;
  box-shadow: inset 0 1px 2px #daa52033, 0 1px 3px #8b45131a;
}

.phrase-highlight:before {
  content: "";
  z-index: -1;
  filter: blur(1px);
  background: radial-gradient(at 60% 20%, #daa52026 0%, #0000 40%), radial-gradient(at 30% 90%, #cd853f1a 0%, #0000 40%);
  border-radius: 8px;
  position: absolute;
  inset: -1px;
}

.macondo-regular {
  font-family: Macondo, cursive;
  font-style: normal;
  font-weight: 400;
}

.cinzel-decorative {
  font-family: Cinzel Decorative, cursive;
  font-style: normal;
  font-weight: 700;
}

.gutenberg-link {
  color: #8b4513;
  opacity: .8;
  font-size: .9em;
  text-decoration: none;
  transition: opacity .2s, color .2s;
}

.gutenberg-link:hover {
  color: #daa520;
  opacity: 1;
  text-decoration: underline;
}

#text, #animation {
  text-align: center;
  user-select: text;
  -webkit-touch-callout: default;
  -webkit-tap-highlight-color: transparent;
  font-family: Macondo, cursive;
  font-size: 22px;
  font-weight: 400;
  line-height: 1.6;
}

#relatedItems {
  color: #666;
  text-align: center;
  max-width: 100%;
  margin-bottom: 10px;
  font-size: 12px;
}

#score-container {
  flex-wrap: nowrap;
  justify-content: space-around;
  width: 100%;
  margin-bottom: 10px;
  padding: 5px 0;
}

#sourceContainer {
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 5px;
  display: flex;
}

#titlerow, #otherRows, #bottomRow {
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  width: 100%;
  margin-bottom: 8px;
  display: flex;
}

.relatedItems {
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  background-color: #0000;
  border-radius: 5px;
  flex: 1;
  padding: 8px 4px;
  font-family: inherit;
  overflow: hidden;
}

#relatedAuthor {
  color: #333;
