  font-size: 13px;
  font-style: italic;
  font-weight: normal;
}

#relatedId, #relatedScore {
  color: #666;
  font-size: 12px;
  font-style: italic;
}

.empty-slot {
  visibility: hidden;
}

.sidebar {
  background-color: #f6f1a5;
  flex-direction: column;
  width: 200px;
  height: 100vh;
  padding: 10px;
  display: flex;
}

#toggleSidebar {
  align-self: flex-start;
}

.score-box {
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  flex: none;
  align-items: center;
  min-width: 100px;
  margin: 2px;
  padding: 5px;
  display: flex;
  box-shadow: 0 2px 5px #0000001a;
}

.score-row {
  flex-wrap: nowrap;
  justify-content: space-around;
  display: flex;
  overflow-x: auto;
}

.score-column {
  flex-direction: column;
  align-items: center;
  display: flex;
  overflow-y: auto;
}

.score-label {
  margin-bottom: 5px;
  font-size: .9em;
  font-weight: bold;
}

.score-value {
