}

.highlightSafe {
  white-space: pre-wrap;
  background: linear-gradient(100deg, #b8ffaf00 1%, #b8ffaf 2.5%, #b8ffaf80 5.7%, #b8ffaf1a 93%, #b8ffafb4 95%, #b8ffaf00 98%), linear-gradient(182deg, #b8ffaf00, #b8ffaf4d 8%, #b8ffaf00 15%);
  display: inline-block;
}

::selection {
  color: #2d1810;
  background: #daa52080;
}

#scatterplot svg {
  width: 100%;
  height: 100%;
}

.point {
  fill: #c0c0f0;
  opacity: 0;
}

.point.highlighted {
  fill: pink;
  stroke: #000;
  opacity: .8;
}

.point.latest {
  fill: #ffbf00;
  stroke: #000;
  opacity: 1;
}

.point-label {
  opacity: .8;
  text-anchor: middle;
  fill: #2d1f04;
  font-size: 10px;
}

.connection-line {
  stroke: #000;
  stroke-width: 1px;
  stroke-dasharray: 4 4;
  opacity: .5;
  fill: none;
}

.cluster-icon {
  pointer-events: none;
